<template>
  <div class="game-controls">
    <button 
      :disabled="!canEndTurn || isDeckEmpty" 
      @click="handleEndTurn"
      :class="{ 'disabled': !canEndTurn || isDeckEmpty }"
    >
      {{ buttonText }}
    </button>
  </div>
  
  <!--  -->
  <div v-if="isDeckEmpty && gameStore.phase === 'deployment'" class="empty-deck-tip">
    
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue';
import { useGameStore } from '../store/gameStore';
import { useDebounceFn } from '@vueuse/core';

const gameStore = useGameStore();

// 
const canEndTurn = computed(() => {
  // 
  return gameStore.phase === 'deployment';
});

// 
const isDeckEmpty = computed(() => {
  return gameStore.deck.length === 0;
});

// 
const buttonText = computed(() => {
  // """"
  if (gameStore.phase === 'deployment' && gameStore.player.energy > 0) {
    return '';
  } else if (gameStore.phase === 'deployment') {
    return '';
  } else {
    return '';
  }
});

// VueUseuseDebounceFn
const handleEndTurn = useDebounceFn(() => {
  if (canEndTurn.value) {
    if (isDeckEmpty.value) {
      gameStore.showMessage('');
      return;
    }
    gameStore.endTurn();
  }
}, 300); // 300ms
</script>

<style scoped>
.game-controls {
  padding: 10px;
  display: flex;
  justify-content: center;
  gap: 20px; /*  */
  margin-top: 15px;
}

button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

button:hover:not(.disabled) {
  background-color: #45a049;
}

button.disabled {
  background-color: #cccccc;
  color: #666666;
  cursor: not-allowed;
}

.empty-deck-tip {
  text-align: center;
  color: #e74c3c;
  background-color: rgba(231, 76, 60, 0.1);
  padding: 6px 10px;
  margin: 6px auto;
  border-radius: 4px;
  max-width: 300px;
  font-size: 14px;
}
</style> 