import { defineStore } from 'pinia'
import { ref, computed, onMounted, onUnmounted } from 'vue'
import type { Card, MinionCard, SpellCard, BuffCard } from '../types/Card'
import { SudokuGenerator } from '../utils/SudokuGenerator'
import { generateEnemiesForLevel, getLevel, LevelConfig } from '../levels/levelManager'

/**
 * 
 * 1.  (sudokuGameStore)
 * 2.  (cardGameStore)
 */

// -----  -----

// 
export interface Unit {
    id: string;
    name: string;
    atk: number;
    hp: number;
    maxHp: number;
    traits: string[];
    cellIndex: number;
    image?: string;
    number?: number; // 
}

// 
export interface Cell {
    index: number;
    occupied: boolean;
    unit?: Unit;
    bonuses?: {
        atk?: number;
        hp?: number;
        defense?: number;
    }
    value: number;
}

// 
export interface BattleLogMessage {
  text: string;
  type: 'info' | 'damage' | 'heal' | 'special';
}

// 
export const useSudokuGameStore = defineStore('sudokuGame', () => {
    // 
    const turn = ref(1)
    const phase = ref<'deployment' | 'battle' | 'victory' | 'defeat'>('deployment')
    const currentBattle = ref(1)
    const completedBattles = ref(0)
    const message = ref<string | null>(null)
    let messageTimeoutId: number | null = null
    const currentLevel = ref(1) // ID

    // 
    const player = ref({
        hp: 30,
        maxHp: 30,
        atk: 2,
        armor: 0,
        gold: 0,
        energy: 1,
        maxEnergy: 1
    })

    // 
    const deck = ref<Card[]>([])
    const hand = ref<Card[]>([])
    const discard = ref<Card[]>([])
    const selectedCard = ref<Card | null>(null)

    // 
    const sudokuPuzzle = ref<number[][]>([])
    const sudokuSolution = ref<number[][]>([])

    // 
    const grid = ref<Cell[]>(Array(81).fill(null).map((_, i) => ({ 
        index: i, 
        occupied: false,
        value: 0
    })))
    const playerUnits = ref<Unit[]>([])
    const enemyUnits = ref<Unit[]>([])

    // 
    const battleLog = ref<BattleLogMessage | null>(null);
    const battleLogHistory = ref<BattleLogMessage[]>([]);

    // 
    function startNewGame(forceReset: boolean = false): boolean {
        // 
        if (!forceReset && loadGameState()) {
            showMessage('')
            return true; // 
        }

        // 
        turn.value = 1
        phase.value = 'deployment'
        currentBattle.value = 1
        completedBattles.value = 0
        currentLevel.value = 1 // 
        
        player.value = {
            hp: 30,
            maxHp: 30,
            atk: 2,
            armor: 0,
            gold: 0,
            energy: 1,
            maxEnergy: 1
        }
        
        // 
        const levelConfig = getLevel(currentLevel.value);
        if (levelConfig) {
            generateSudokuPuzzle(levelConfig.sudokuDifficulty);
        } else {
            // 
            generateSudokuPuzzle(0.3);
            console.warn(`${currentLevel.value}`);
        }
        
        // resetGrid
        resetGrid();
        
        playerUnits.value = []
        enemyUnits.value = []
        
        // 
        initializeDeck()
        drawCards(3)
        
        // 
        spawnEnemies()
        
        showMessage('')
        
        // 
        saveGameState()
        
        return false; // false
    }
    
    // 
    function generateSudokuPuzzle(difficulty: number = 0.3) {
        const generator = new SudokuGenerator()
        const { puzzle, solution } = generator.generate(difficulty)
        sudokuPuzzle.value = puzzle
        sudokuSolution.value = solution
    }
    
    // 
    function initializeDeck() {
        const initialDeck: Card[] = [
            //  - 15
            { id: '1', name: '', description: '2/2', type: 'unit', cost: 1, rarity: 'common', image: 'assets/demon.svg', value: 2, hp: 2, atk: 2 },
            { id: '2', name: '', description: '3/3', type: 'unit', cost: 1, rarity: 'common', image: 'assets/demon-warrior.svg', value: 3, hp: 3, atk: 3 },
            { id: '3', name: '', description: '2/3', type: 'unit', cost: 1, rarity: 'common', image: 'assets/hellhound.svg', value: 3, hp: 3, atk: 2 },
            { id: '4', name: '', description: '1/4', type: 'unit', cost: 1, rarity: 'common', image: 'assets/skeleton.svg', value: 1, hp: 4, atk: 1 },
            { id: '5', name: '', description: '3/2', type: 'unit', cost: 1, rarity: 'common', image: 'assets/demon-archer.svg', value: 3, hp: 2, atk: 3 },
            { id: '6', name: '', description: '2/2', type: 'unit', cost: 1, rarity: 'common', image: 'assets/ghost.svg', value: 2, hp: 2, atk: 2 },
            { id: '7', name: '', description: '1/5', type: 'unit', cost: 1, rarity: 'uncommon', image: 'assets/demon-mage.svg', value: 4, hp: 5, atk: 1 },
            { id: '8', name: '', description: '4/4', type: 'unit', cost: 1, rarity: 'rare', image: 'assets/demon-lord.svg', value: 6, hp: 4, atk: 4 },
            { id: '9', name: '', description: '3/5', type: 'unit', cost: 1, rarity: 'rare', image: 'assets/hell-knight.svg', value: 6, hp: 5, atk: 3 },
            { id: '10', name: '', description: '2/6', type: 'unit', cost: 1, rarity: 'uncommon', image: 'assets/demon-guard.svg', value: 5, hp: 6, atk: 2 },
            { id: '11', name: '', description: '5/5', type: 'unit', cost: 1, rarity: 'epic', image: 'assets/bone-dragon.svg', value: 8, hp: 5, atk: 5 },
            { id: '12', name: '', description: '3/7', type: 'unit', cost: 1, rarity: 'rare', image: 'assets/blood-demon.svg', value: 7, hp: 7, atk: 3 },
            { id: '13', name: '', description: '4/6', type: 'unit', cost: 1, rarity: 'epic', image: 'assets/death-knight.svg', value: 8, hp: 6, atk: 4 },
            { id: '14', name: '', description: '6/6', type: 'unit', cost: 1, rarity: 'legendary', image: 'assets/inferno-lord.svg', value: 9, hp: 6, atk: 6 },
            { id: '15', name: '', description: '8/8', type: 'unit', cost: 1, rarity: 'legendary', image: 'assets/ancient-demon.svg', value: 9, hp: 8, atk: 8 },
            
            //  - 10
            { id: '101', name: '', description: '3', type: 'spell', cost: 1, value: 3, rarity: 'common', image: 'assets/fireball.svg' },
            { id: '102', name: '', description: '3', type: 'spell', cost: 1, value: 3, rarity: 'common', image: 'assets/heal.svg' },
            { id: '103', name: '', description: '2', type: 'spell', cost: 1, value: 2, rarity: 'common', image: 'assets/lightning.svg' },
            { id: '104', name: '', description: '2', type: 'spell', cost: 1, value: 2, rarity: 'common', image: 'assets/buff.svg' },
            { id: '105', name: '', description: '3', type: 'spell', cost: 1, value: 3, rarity: 'common', image: 'assets/armor.svg' },
            { id: '106', name: '', description: '2', type: 'spell', cost: 1, value: 0, rarity: 'rare', image: 'assets/firestorm.svg' },
            { id: '107', name: '', description: '2', type: 'spell', cost: 1, value: 2, rarity: 'uncommon', image: 'assets/draw.svg' },
            { id: '108', name: '', description: '5', type: 'spell', cost: 1, value: 5, rarity: 'uncommon', image: 'assets/lava-burst.svg' },
            { id: '109', name: '', description: '2', type: 'spell', cost: 1, value: 2, rarity: 'rare', image: 'assets/mass-heal.svg' },
            { id: '110', name: '', description: '1', type: 'spell', cost: 1, value: 1, rarity: 'rare', image: 'assets/holy-power.svg' },
            
            //  - 5
            { id: '201', name: '', description: '1', type: 'buff', cost: 1, value: 1, rarity: 'uncommon', image: 'assets/energy-rune.svg' },
            { id: '202', name: '', description: '+1', type: 'buff', cost: 1, value: 1, rarity: 'uncommon', image: 'assets/battle-cry.svg' },
            { id: '203', name: '', description: '5', type: 'buff', cost: 1, value: 5, rarity: 'rare', image: 'assets/divine-shield.svg' },
            { id: '204', name: '', description: '3', type: 'buff', cost: 1, value: 3, rarity: 'rare', image: 'assets/mana-surge.svg' },
            { id: '205', name: '', description: '+2', type: 'buff', cost: 1, value: 2, rarity: 'epic', image: 'assets/spell-amplify.svg' }
        ]
        
        deck.value = [...initialDeck]
        shuffleDeck()
        
        hand.value = []
        discard.value = []
    }
    
    // 
    function shuffleDeck() {
        for (let i = deck.value.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [deck.value[i], deck.value[j]] = [deck.value[j], deck.value[i]];
        }
    }
    
    // 
    function drawCards(count: number) {
        if (deck.value.length === 0 && discard.value.length > 0) {
            // 
            shuffleDiscardIntoDeck()
        }
        
        // 
        const actualCount = Math.min(count, deck.value.length)
        
        // 
        for (let i = 0; i < actualCount; i++) {
            const card = deck.value.pop()
            if (card) {
                hand.value.push(card)
            }
        }
        
        // UI
        updateGameEngine()
    }
    
    // 
    function playCard(cardIndex: number, cellIndex: number) {
        if (cardIndex < 0 || cardIndex >= hand.value.length) return

        const card = hand.value[cardIndex]
        
        // 
        if (card.cost > player.value.energy) {
            showMessage('')
            return
        }
        
        // 
        player.value.energy -= card.cost
        
        // 
        if (card.type === 'unit') {
            deployUnit(card, cellIndex)
        } else if (card.type === 'spell') {
            if (cellIndex !== -1) {
                // 
                useSpell(card, cellIndex)
            } else {
                // 
                useGlobalSpell(card)
            }
        }
        
        // 
        const playedCard = hand.value.splice(cardIndex, 1)[0]
        
        // 
        discard.value.push(playedCard)
        
        // UI
        updateGameEngine()
    }
    
    // 
    function deployUnit(card: Card, cellIndex: number) {
        // 
        if (card.type !== 'unit' || !('hp' in card) || !('atk' in card)) return
        
        // 
        const cell = grid.value[cellIndex]
        
        // 
        if (cell.occupied) {
            showMessage('')
            return
        }
        
        // 
        const row = Math.floor(cellIndex / 9);
        const col = cellIndex % 9;
        
        const unit: Unit = {
            id: card.id + Date.now(),
            name: card.name,
            hp: card.hp ?? 1,
            maxHp: card.hp ?? 1,
            atk: card.atk ?? 1,
            traits: [],
            cellIndex: cellIndex,
            image: card.image,
            number: card.value || 0 
        }
        
        // 
        playerUnits.value.push(unit)
        
        // 
        grid.value[cellIndex].occupied = true
        grid.value[cellIndex].unit = unit
        
        console.log(` ${cellIndex} (${row}${col})  ${unit.name}: ${unit.number}`);
        
        if (grid.value[cellIndex].unit) {
          saveGameState(); // 
        }
    }
    
    // 
    function useSpell(card: Card, targetIndex: number) {
        const targetCell = grid.value[targetIndex]
        
        if (card.value !== undefined) {
            if (targetCell.unit && targetCell.unit.id.startsWith('enemy')) {
                // 
                const damage = card.value
                targetCell.unit.hp -= damage
                
                showMessage(` ${targetCell.unit.name}  ${damage} `)
                // 
                addBattleLog(` ${card.name}  ${targetCell.unit.name}  ${damage} `, 'damage');
                
                // 
                if (targetCell.unit.hp <= 0) {
                    addBattleLog(`${targetCell.unit.name} `, 'special');
                    removeUnit(targetCell.unit.id)
                    showMessage(`${targetCell.unit.name} `)
                }
            } else if (targetCell.unit && targetCell.unit.id.startsWith('player')) {
                // ()
                // 
                if (card.description.includes('')) {
                    const healing = card.value
                    targetCell.unit.hp = Math.min(targetCell.unit.hp + healing, targetCell.unit.maxHp)
                    showMessage(` ${targetCell.unit.name} ${healing} `)
                    addBattleLog(` ${card.name}  ${targetCell.unit.name} ${healing} `, 'heal');
                } else if (card.description.includes('')) {
                    targetCell.unit.atk += card.value
                    showMessage(` ${targetCell.unit.name} ${card.value} `)
                    addBattleLog(` ${card.name}  ${targetCell.unit.name} ${card.value} `, 'special');
                }
            }
        }
        
        if (card.name === '') {
            const targetUnit = grid.value[targetIndex].unit;
            if (targetUnit) {
                const healAmount = card.value;
                targetUnit.hp = Math.min(targetUnit.hp + healAmount, targetUnit.maxHp);
                addBattleLog(`${targetUnit.name}  ${healAmount} `, 'heal');
            }
        }
    }
    
    // 
    function useGlobalSpell(card: Card) {
        if (card.description.includes('') && card.value !== undefined) {
            player.value.armor += card.value
            showMessage(` ${card.value} `)
        } else if (card.description.includes('') && card.value !== undefined) {
            drawCards(card.value)
            showMessage(` ${card.value} `)
        } else if (card.description.includes('') && card.value !== undefined) {
            // 
            for (const unit of enemyUnits.value) {
                unit.hp -= card.value
                if (unit.hp <= 0) {
                    removeUnit(unit.id)
                }
            }
            
            // 
            enemyUnits.value = enemyUnits.value.filter(unit => unit.hp > 0)
            
            showMessage(` ${card.value} `)
        }
    }
    
    // 
    function removeUnit(unitId: string) {
        // 
        const playerUnitIndex = playerUnits.value.findIndex(u => u.id === unitId)
        if (playerUnitIndex >= 0) {
            const unit = playerUnits.value[playerUnitIndex]
            playerUnits.value.splice(playerUnitIndex, 1)
            
            // 
            createNeutralUnit(unit.cellIndex, grid.value[unit.cellIndex].value)
            return
        }
        
        // 
        const enemyUnitIndex = enemyUnits.value.findIndex(u => u.id === unitId)
        if (enemyUnitIndex >= 0) {
            const unit = enemyUnits.value[enemyUnitIndex]
            const cellIndex = unit.cellIndex
            enemyUnits.value.splice(enemyUnitIndex, 1)
            
            // 
            createNeutralUnit(cellIndex, grid.value[cellIndex].value)
            return
        }
    }
    
    // 
    function createNeutralUnit(cellIndex: number, cellValue: number) {
        // 
        const cell = grid.value[cellIndex];
        
        // 
        const neutralUnit: Unit = {
            id: `neutral_${Date.now()}_${cellIndex}`,
            name: "",
            hp: 1,
            maxHp: 1,
            atk: 0,
            traits: [],
            cellIndex: cellIndex,
            image: 'assets/neutral.svg', // 
            number: cellValue || 0 // 
        };
        
        // 
        cell.unit = neutralUnit;
        cell.occupied = true;
        
        console.log(` ${cellIndex} : ${cellValue}`);
    }
    
    // 
    function spawnEnemies() {
        // 
        const levelConfig = getLevel(currentLevel.value);
        if (!levelConfig) {
            console.error(`: ${currentLevel.value}`);
            return;
        }
        
        // 
        enemyUnits.value = [];
        
        // 0
        const availableCells = grid.value.filter(cell => !cell.occupied && cell.value > 0);
        
        if (availableCells.length === 0) {
            console.warn('');
            // 
            const allEmptyCells = grid.value.filter(cell => !cell.occupied);
            if (allEmptyCells.length === 0) {
                console.error('');
                return;
            }
            // 
            generateEnemiesOnCells(levelConfig, allEmptyCells);
            return;
        }
        
        // 
        generateEnemiesOnCells(levelConfig, availableCells);
    }
    
    // 
    function resetGrid() {
        // 
        if (!sudokuPuzzle.value || !sudokuPuzzle.value.length) {
            console.error('');
            return;
        }
        
        // 
        grid.value = [];
        
        // 
        for (let i = 0; i < 81; i++) {
            const row = Math.floor(i / 9);
            const col = i % 9;
            
            // 
            const puzzleValue = sudokuPuzzle.value[row][col];
            
            // 
            grid.value.push({ 
                index: i, 
                occupied: false,
                value: puzzleValue // 
            });
        }
        
        // 
        const nonZeroCount = grid.value.filter(cell => cell.value > 0).length;
        const zeroCount = grid.value.filter(cell => cell.value === 0).length;
        console.log(`${grid.value.length}${nonZeroCount}${zeroCount}`);
    }
    
    // 
    function generateEnemiesOnCells(levelConfig: LevelConfig, availableCells: Cell[]) {
        if (!levelConfig || !levelConfig.enemies || levelConfig.enemies.length === 0) {
            console.error('');
            return;
        }
        
        if (!availableCells || availableCells.length === 0) {
            console.error('');
            return;
        }
        
        // 
        const shuffledCells = [...availableCells].sort(() => Math.random() - 0.5);
        
        // 
        const enemiesToGenerate = Math.min(levelConfig.enemies.length, shuffledCells.length);
        
        // 
        console.log(` ${currentLevel.value} : ${levelConfig.enemies.length}`);
        console.log(`: ${shuffledCells.length}`);
        console.log(` ${enemiesToGenerate} `);
        
        // 
        let enemyCount = 0;
        
        for (let i = 0; i < enemiesToGenerate; i++) {
            try {
                const enemyConfig = levelConfig.enemies[i];
                const cellToUse = shuffledCells[i];
                
                if (!cellToUse) {
                    console.warn(`${i+1}`);
                    continue;
                }
                
                // ID
                const enemyId = `enemy_${currentLevel.value}_${i}_${Date.now()}`;
                
                // 
                const row = Math.floor(cellToUse.index / 9);
                const col = cellToUse.index % 9;
                
                // 
                const cellValue = sudokuPuzzle.value[row][col];
                
                // 
                const enemyUnit: Unit = {
                    id: enemyId,
                    name: enemyConfig.name,
                    hp: enemyConfig.hp,
                    maxHp: enemyConfig.hp,
                    atk: enemyConfig.atk,
                    traits: [],
                    cellIndex: cellToUse.index,
                    image: enemyConfig.image || 'assets/enemy_default.svg',
                    number: cellValue || 0
                };
                
                // 
                enemyUnits.value.push(enemyUnit);
                
                // 
                grid.value[cellToUse.index].occupied = true;
                grid.value[cellToUse.index].unit = enemyUnit;
                
                enemyCount++;
                
                console.log(` ${cellToUse.index} (${row}${col})  ${enemyUnit.name}: ${cellValue}`);
            } catch (error) {
                console.error(`${i+1}:`, error);
            }
        }
        
        // 
        console.log(` ${currentLevel.value} (${levelConfig.name})  ${enemyCount} `);
        
        // 
        showMessage(`${currentLevel.value}: ${levelConfig.name} - : ${enemyCount}`);
    }
    
    // 
    function endTurn() {
        if (phase.value === 'deployment') {
            const cardsDiscarded = discardHand();
            if (cardsDiscarded > 0) {
              showMessage('');
            }
            saveGameState(); // 
            startNextTurn();
        } else {
            // 
            console.warn(`endTurn called during unexpected phase: ${phase.value}`);
        }
    }
    
    // 
    function startNextTurn() {
        // 
        turn.value++;
        
        // 
        player.value.energy = player.value.maxEnergy;
        
        // 
        drawCards(3);
        
        showMessage(` ${turn.value} `);
    }
    
    // 
    function startBattlePhase() {
        phase.value = 'battle';
        showMessage('');
        // 
        addBattleLog('', 'special');
        
        // 
        saveGameState();
        executeBattle();
    }
    
    // 
    function executeBattle() {
        // 
        addBattleLog('...', 'info');
        
        // 
        playerUnits.value.forEach(playerUnit => {
            enemyUnits.value.forEach(enemyUnit => {
                // 
                const damage = playerUnit.atk;
                enemyUnit.hp -= damage;
                
                // 
                addBattleLog(`${playerUnit.name}  ${enemyUnit.name}  ${damage} `, 'damage');
                
                // 
                if (enemyUnit.hp <= 0) {
                    addBattleLog(`${enemyUnit.name} `, 'special');
                    removeUnit(enemyUnit.id);
                }
            });
        });
        
        // 
        enemyUnits.value.forEach(enemyUnit => {
            playerUnits.value.forEach(playerUnit => {
                const damage = enemyUnit.atk;
                playerUnit.hp -= damage;
                
                addBattleLog(`${enemyUnit.name}  ${playerUnit.name}  ${damage} `, 'damage');
                
                if (playerUnit.hp <= 0) {
                    addBattleLog(`${playerUnit.name} `, 'special');
                    removeUnit(playerUnit.id);
                }
            });
        });
        
        // 
        if (enemyUnits.value.length === 0) {
            addBattleLog('', 'special');
            handleVictory();
        } else if (playerUnits.value.length === 0) {
            addBattleLog('', 'special');
            handleDefeat();
        } else {
            // 
            addBattleLog('', 'info');
        }
    }
    
    // 
    function handleVictory() {
        phase.value = 'victory';
        
        // 
        addBattleLog('', 'special');
        
        // 
        const level = getLevel(currentLevel.value);
        if (level && level.rewards) {
            if (level.rewards.gold) {
                player.value.gold += level.rewards.gold;
                showMessage(` ${level.rewards.gold} `);
                addBattleLog(` ${level.rewards.gold} `, 'special');
            }
        }
        
        showMessage('');
        
        // 
        const hasNextLevel = getLevel(currentLevel.value + 1) !== undefined;
        
        // 
        if (hasNextLevel) {
            showMessage('...');
            addBattleLog('...', 'info');
            setTimeout(() => {
                currentLevel.value++;
                prepareNextBattle();
            }, 2000);
        } else {
            showMessage('');
            addBattleLog('', 'special');
        }
        
        saveGameState();
    }
    
    // 
    function handleDefeat() {
        phase.value = 'defeat';
        
        showMessage('...');
        addBattleLog('...', 'damage');
        addBattleLog('', 'info');
        
        // 3
        localStorage.removeItem('sudokuGameState');
        setTimeout(() => {
            startNewGame(true);
        }, 3000);
    }
    
    // 
    function prepareNextBattle() {
        // 
        addBattleLog(` ${currentLevel.value} `, 'info');
        
        // 
        phase.value = 'deployment';
        turn.value = 1;
        currentBattle.value++;
        
        // 
        player.value.energy = player.value.maxEnergy;
        
        // 
        const levelConfig = getLevel(currentLevel.value);
        if (levelConfig) {
            generateSudokuPuzzle(levelConfig.sudokuDifficulty);
        } else {
            // 
            generateSudokuPuzzle(0.3 + currentLevel.value * 0.05); // 
            console.warn(`${currentLevel.value}`);
        }
        
        // 
        resetGrid();
        
        playerUnits.value = [];
        enemyUnits.value = [];
        
        // 
        hand.value = [];
        discard.value = [];
        drawCards(3);
        
        // 
        spawnEnemies();
        
        // 
        if (levelConfig) {
            showMessage(`${currentLevel.value}: ${levelConfig.name}`);
            addBattleLog(`${currentLevel.value}: ${levelConfig.name}`, 'special');
        } else {
            showMessage(`${currentLevel.value}`);
            addBattleLog(`${currentLevel.value}`, 'special');
        }
    }
    
    // 
    function shuffleDiscardIntoDeck() {
        deck.value.push(...discard.value)
        discard.value = []
        shuffleDeck()
        
        showMessage('')
        updateGameEngine()
    }
    
    // UI
    function updateGameEngine() {
        if (!window.gameEngine) return
        
        // gameEnginegameStateupdateGameState
        const gameState = {
            player: {
                health: player.value.hp,
                maxHealth: player.value.maxHp,
                mana: player.value.energy,
                maxMana: player.value.maxEnergy,
                hand: hand.value,
                deck: deck.value.length,
                discard: discard.value.length
            },
            enemy: {
                health: 100,
                maxHealth: 100
            },
            battle: {
                currentTurn: turn.value,
                activePlayer: phase.value
            }
        }
        
        // updateGameState
        if (typeof window.gameEngine.updateGameState === 'function') {
            window.gameEngine.updateGameState(gameState)
        } 
        // gameState
        else if (window.gameEngine) {
            // 
            (window.gameEngine as any).gameState = gameState
        }
    }
    
    // 
    function showMessage(msg: string, duration = 3000) {
        message.value = msg
        
        // 
        if (messageTimeoutId !== null) {
            clearTimeout(messageTimeoutId)
        }
        
        // 
        messageTimeoutId = window.setTimeout(() => {
            message.value = null
            messageTimeoutId = null
        }, duration) as unknown as number
    }
    
    // 
    const availableCells = computed(() => {
        return grid.value.filter(cell => !cell.occupied)
    })
    
    // HP
    const enemyHpPercent = computed(() => {
        if (enemyUnits.value.length === 0) return 0
        
        // 
        const enemy = enemyUnits.value[0]
        return (enemy.hp / enemy.maxHp) * 100
    })
    
    // 
    function saveGameState() {
        try {
            // 
            console.log(`... : ${playerUnits.value.length}, : ${enemyUnits.value.length}`);
            
            const gameState = {
                turn: turn.value,
                phase: phase.value,
                currentBattle: currentBattle.value,
                completedBattles: completedBattles.value,
                currentLevel: currentLevel.value,
                player: player.value,
                sudokuPuzzle: sudokuPuzzle.value,
                sudokuSolution: sudokuSolution.value,
                // 
                grid: grid.value.map(cell => ({
                    index: cell.index,
                    occupied: cell.occupied,
                    value: cell.value,
                    // unitplayerUnits
                })),
                // 
                playerUnits: playerUnits.value,
                enemyUnits: enemyUnits.value,
                deck: deck.value,
                hand: hand.value,
                discard: discard.value
            };
            
            localStorage.setItem('sudokuGameState', JSON.stringify(gameState));
            console.log(`: ${gameState.playerUnits.length}, : ${gameState.enemyUnits.length}`);
            return true;
        } catch (error) {
            console.error(':', error);
            return false;
        }
    }
    
    // 
    function loadGameState(): boolean {
        try {
            const savedState = localStorage.getItem('sudokuGameState');
            if (!savedState) {
                console.log("");
                return false;
            }
            
            console.log("...");
            const gameState = JSON.parse(savedState);
            
            // ---  ---
            turn.value = gameState.turn;
            phase.value = gameState.phase;
            currentBattle.value = gameState.currentBattle;
            completedBattles.value = gameState.completedBattles;
            currentLevel.value = gameState.currentLevel;
            player.value = gameState.player;
            sudokuPuzzle.value = gameState.sudokuPuzzle;
            sudokuSolution.value = gameState.sudokuSolution;
            
            // ---  ---
            deck.value = gameState.deck || [];
            hand.value = gameState.hand || [];
            discard.value = gameState.discard || [];
            selectedCard.value = null; // 

            // ---  ---
            
            // 1.  (value, occupied - )
            const initialGrid = Array(81).fill(null).map((_, i) => ({
                index: i,
                occupied: false, // false
                value: 0,
                unit: undefined // unit
            }));
            if (gameState.grid) {
                for (let i = 0; i < gameState.grid.length; i++) {
                    if (initialGrid[i]) { // 
                        initialGrid[i].value = gameState.grid[i]?.value || 0;
                    }
                }
            }
            grid.value = initialGrid;

            // 2. 
            playerUnits.value = gameState.playerUnits || [];
            enemyUnits.value = gameState.enemyUnits || [];

            console.log(` ${playerUnits.value.length}  ${enemyUnits.value.length} `);

            // 3. occupied
            const linkUnitToCell = (unit: Unit) => {
                if (unit.cellIndex !== undefined && unit.cellIndex >= 0 && unit.cellIndex < 81) {
                    const cell = grid.value[unit.cellIndex];
                    if (cell) {
                        cell.unit = unit;
                        cell.occupied = true; // 
                        console.log(` ${unit.name} (ID: ${unit.id})  ${cell.index}`);
                    } else {
                        console.warn(` ${unit.name} (ID: ${unit.id}) : ${unit.cellIndex}`);
                    }
                } else {
                    console.warn(` ${unit.name} (ID: ${unit.id})  cellIndex : ${unit.cellIndex}`);
                }
            };

            playerUnits.value.forEach(linkUnitToCell);
            enemyUnits.value.forEach(linkUnitToCell);

            const linkedPlayerUnits = grid.value.filter(c => c.unit && playerUnits.value.some(pu => pu.id === c.unit?.id)).length;
            const linkedEnemyUnits = grid.value.filter(c => c.unit && enemyUnits.value.some(eu => eu.id === c.unit?.id)).length;
            console.log(` ${linkedPlayerUnits}  ${linkedEnemyUnits} `);

            showMessage('');
            console.log(', :', currentLevel.value);
            console.log(`: ${deck.value.length}, : ${hand.value.length}, : ${discard.value.length}`);
            return true;
        } catch (error) {
            console.error(':', error);
            // 
            localStorage.removeItem('sudokuGameState'); 
            return false;
        }
    }
    
    // 
    function discardHand() {
        const cardsDiscarded = hand.value.length;
        if (cardsDiscarded > 0) {
            discard.value.push(...hand.value);
            hand.value = [];
        }
        return cardsDiscarded;
    }

    // 
    function selectCard(cardId: string | null) {
        if (cardId === null) {
            selectedCard.value = null;
            return;
        }
        
        selectedCard.value = hand.value.find(card => card.id === cardId) || null;
    }
    
    // ---  ---
    const saveBeforeUnload = () => {
      console.log('...');
      saveGameState();
    };

    onMounted(() => {
        window.addEventListener('beforeunload', saveBeforeUnload);
    });

    onUnmounted(() => {
        window.removeEventListener('beforeunload', saveBeforeUnload);
        // 
        saveGameState();
    });

    // 
    function addBattleLog(text: string, type: BattleLogMessage['type'] = 'info') {
        console.log(`[BattleLog] ${type}: ${text}`);
        
        const message: BattleLogMessage = { text, type };
        battleLog.value = message;
        battleLogHistory.value.push(message);
        
        // 100
        if (battleLogHistory.value.length > 100) {
            battleLogHistory.value.shift();
        }
        
        // 
        console.log(`: ${battleLogHistory.value.length}`);
    }

    //  - 
    function addTestBattleLogs() {
        console.log("");
        addBattleLog("", "special");
        addBattleLog("    2 ", "damage");
        addBattleLog("    1 ", "damage");
        addBattleLog("  3 ", "heal");
        
        // 
        console.log(`: ${battleLogHistory.value.length}`);
        console.log(battleLogHistory.value);
    }

    return {
        // 
        turn,
        phase,
        currentBattle,
        completedBattles,
        message,
        currentLevel,
        player,
        
        deck,
        hand,
        discard,
        selectedCard,
        
        sudokuPuzzle,
        sudokuSolution,
        
        grid,
        playerUnits,
        enemyUnits,
        
        availableCells,
        enemyHpPercent,
        
        // 
        startNewGame,
        generateSudokuPuzzle,
        resetGrid,
        initializeDeck,
        drawCards,
        shuffleDeck,
        discardHand,
        selectCard,
        playCard,
        deployUnit,
        useSpell,
        useGlobalSpell,
        removeUnit,
        shuffleDiscardIntoDeck,
        updateGameEngine,
        showMessage,
        endTurn,
        startNextTurn,
        startBattlePhase,
        executeBattle,
        handleVictory,
        handleDefeat,
        prepareNextBattle,
        spawnEnemies,
        
        // 
        saveGameState,
        loadGameState,
        battleLog,
        battleLogHistory,
        addBattleLog,
        addTestBattleLogs,
    }
})

// -----  -----

export const useCardGameStore = defineStore('cardGame', () => {
  // 
  const playerHealth = ref(30);
  const playerMaxHealth = ref(30);
  const playerMana = ref(0);
  const playerMaxMana = ref(10);
  const playerHand = ref<Card[]>([]);
  const playerDeck = ref<Card[]>([]);
  const playerDiscardPile = ref<Card[]>([]);

  // 
  const enemyHealth = ref(30);
  const enemyMaxHealth = ref(30);
  const enemyIntent = ref<{ type: string; value: number }>({ type: 'attack', value: 5 });

  // 
  const currentPhase = ref<'draw' | 'play' | 'enemy' | 'end'>('draw');
  const currentTurn = ref(1);
  const isCardBeingPlayed = ref(false);
  const gameMessage = ref('');

  // 
  const currentMana = computed(() => playerMana.value);
  const playerHealthPercentage = computed(() => (playerHealth.value / playerMaxHealth.value) * 100);
  const enemyHealthPercentage = computed(() => (enemyHealth.value / enemyMaxHealth.value) * 100);

  // 
  const drawCard = (count: number = 1) => {
    for (let i = 0; i < count; i++) {
      if (playerDeck.value.length === 0) {
        shuffleDiscardIntoDeck();
        if (playerDeck.value.length === 0) break; // 
      }
      
      const drawnCard = playerDeck.value.pop();
      if (drawnCard) {
        playerHand.value.push(drawnCard);
      }
    }
  };

  // 
  const shuffleDiscardIntoDeck = () => {
    playerDeck.value = [...playerDiscardPile.value].sort(() => Math.random() - 0.5);
    playerDiscardPile.value = [];
    gameMessage.value = '';
  };

  // 
  const playCard = (card: Card) => {
    isCardBeingPlayed.value = true;
    gameMessage.value = ` ${card.name}`;
    
    // 
    const cardIndex = playerHand.value.findIndex(c => c.id === card.id);
    if (cardIndex !== -1) {
      playerHand.value.splice(cardIndex, 1);
    }
    
    // 
    playerMana.value -= card.cost;
    
    // 
    applyCardEffect(card);
    
    // 
    playerDiscardPile.value.push(card);
    
    // 
    setTimeout(() => {
      isCardBeingPlayed.value = false;
    }, 500);
  };

  // 
  const applyCardEffect = (card: Card) => {
    if (card.type === 'spell') {
      // 
      const spellCard = card as SpellCard;
      if (spellCard.damage) {
        // 
        enemyHealth.value = Math.max(0, enemyHealth.value - spellCard.damage);
      } else if (spellCard.healing) {
        // 
        playerHealth.value = Math.min(playerMaxHealth.value, playerHealth.value + spellCard.healing);
      } else if (card.description.includes('')) {
        // 
        drawCard(card.value);
      }
    } else if (card.type === 'unit') {
      // 
      if (card.atk) {
        enemyHealth.value = Math.max(0, enemyHealth.value - card.atk);
      }
    }
    
    // 
    if (enemyHealth.value <= 0) {
      endBattle(true);
    }
  };

  // 
  const endBattle = (playerWon: boolean) => {
    if (playerWon) {
      gameMessage.value = '';
    } else {
      gameMessage.value = '';
    }
  };

  // 
  const initGame = () => {
    // 
    playerHealth.value = 30;
    playerMaxHealth.value = 30;
    playerMana.value = 3;
    playerMaxMana.value = 3;
    currentTurn.value = 1;
    
    // 
    playerDeck.value = generateInitialDeck();
    playerHand.value = [];
    playerDiscardPile.value = [];
    
    // 
    enemyHealth.value = 30;
    enemyMaxHealth.value = 30;
    updateEnemyIntent();
    
    // 
    drawCard(5);
    currentPhase.value = 'play';
    gameMessage.value = '';
  };

  // 
  const generateInitialDeck = (): Card[] => {
    const deck: Card[] = [];
    
    // 10
    for (let i = 0; i < 10; i++) {
      deck.push({
        id: `attack-${i}`,
        name: '',
        description: '5',
        cost: 1,
        type: 'spell',
        value: 0,
        damage: 5,
        rarity: 'common',
        image: '/images/cards/attack.png'
      } as SpellCard);
    }
    
    // 5
    for (let i = 0; i < 5; i++) {
      deck.push({
        id: `defense-${i}`,
        name: '',
        description: '5',
        cost: 1,
        type: 'spell',
        value: 0,
        healing: 5,
        rarity: 'common',
        image: '/images/cards/defense.png'
      } as SpellCard);
    }
    
    // 
    deck.push({
      id: 'skill-draw',
      name: '',
      description: '2',
      cost: 1,
      type: 'spell',
      value: 2,
      rarity: 'uncommon',
      image: '/images/cards/draw.png'
    });
    
    // 
    return deck.sort(() => Math.random() - 0.5);
  };

  // 
  const updateEnemyIntent = () => {
    // 
    const intentTypes = ['attack', 'defend'];
    const type = intentTypes[Math.floor(Math.random() * intentTypes.length)];
    const value = type === 'attack' ? Math.floor(Math.random() * 8) + 3 : Math.floor(Math.random() * 5) + 2;
    
    enemyIntent.value = { type, value };
  };

  //  ()
  const startPlayerTurn = () => {
    currentPhase.value = 'play';
    drawCard(5);
  };
  
  const endPlayerTurn = () => {
    currentPhase.value = 'enemy';
  };

  return {
    // 
    playerHealth,
    playerMaxHealth,
    playerMana,
    playerMaxMana,
    playerHand,
    playerDeck,
    playerDiscardPile,
    enemyHealth,
    enemyMaxHealth,
    enemyIntent,
    currentPhase,
    currentTurn,
    isCardBeingPlayed,
    gameMessage,
    
    // 
    currentMana,
    playerHealthPercentage,
    enemyHealthPercentage,
    
    // 
    drawCard,
    playCard,
    startPlayerTurn,
    endPlayerTurn,
    initGame
  };
}); 