<template>
  <div class="game-start-overlay" v-if="!gameStarted">
    <div class="game-start-container">
      <h1></h1>
      <button v-if="hasSavedGame" class="continue-button" @click="continueGame"></button>
      <button class="start-button" @click="startGame"></button>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue';
import { useGameStore } from '../store/gameStore';

const gameStore = useGameStore();
const gameStarted = ref(false);
const hasSavedGame = ref(false);

function validateEnemyUnits() {
  if (gameStore.enemyUnits.length === 0) {
    console.warn('');
    gameStore.startNewGame(true);
    
    // 
    setTimeout(() => {
      if (gameStore.enemyUnits.length === 0) {
        console.error('');
        // 
        gameStore.showMessage('...');
      } else {
        console.log(':', gameStore.enemyUnits.length);
      }
    }, 500);
  } else {
    console.log(':', gameStore.enemyUnits.length);
  }
}

// 
function startGame() {
  gameStarted.value = true;
  
  // true
  gameStore.startNewGame(true);
  
  // 
  setTimeout(validateEnemyUnits, 100);
  
  console.log(":", gameStore.currentLevel);
}

// 
function continueGame() {
  gameStarted.value = true;
  
  // 
  const success = gameStore.startNewGame(false);
  
  if (!success) {
    console.error("");
    gameStore.startNewGame(true);
    
    // 
    setTimeout(validateEnemyUnits, 100);
  } else {
    console.log(":", gameStore.currentLevel);
    console.log(":", gameStore.playerUnits.length);
    console.log(":", gameStore.enemyUnits.length);
  }
}

//  ()
onMounted(() => {
  console.log("GameStart");
  
  // 
  try {
    const savedState = localStorage.getItem('sudokuGameState');
    if (savedState) {
      const state = JSON.parse(savedState);
      hasSavedGame.value = true;
      console.log(":", state.currentLevel);
    }
  } catch (error) {
    console.error(":", error);
  }
});
</script>

<style scoped>
.game-start-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.game-start-container {
  background-color: rgba(20, 20, 40, 0.9);
  padding: 30px 50px;
  border-radius: 10px;
  text-align: center;
  border: 2px solid #55aaff;
  box-shadow: 0 0 20px rgba(85, 170, 255, 0.5);
}

h1 {
  color: #55aaff;
  margin-bottom: 30px;
  text-shadow: 0 0 10px rgba(85, 170, 255, 0.7);
}

.start-button, .continue-button {
  background-color: #333355;
  color: white;
  border: 2px solid #55aaff;
  padding: 12px 24px;
  font-size: 18px;
  border-radius: 5px;
  cursor: pointer;
  margin: 10px;
  min-width: 150px;
  transition: all 0.3s ease;
}

.start-button:hover, .continue-button:hover {
  background-color: #55aaff;
  transform: scale(1.05);
}

.continue-button {
  background-color: #225533;
  border-color: #55dd77;
}

.continue-button:hover {
  background-color: #55dd77;
}
</style> 